.modalWindow-input {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0,0,0,0.8);
    z-index: 99999;
    opacity: 0;
    -webkit-transition: opacity 400ms ease-in;
    -moz-transition: opacity 400ms ease-in;
    transition: opacity 400ms ease-in;
    pointer-events: none; }

.modalWindow-input:target {
    opacity: 1;
    pointer-events: auto; }

.modalWindow-input > div {
    width: 600px;
    opacity: 1;
    position: relative;
    margin: 10% auto;
    padding: 20px 20px 20px 25px;
    border-radius: 5px;
    background: #fff;
    box-shadow: 0px 0px 10px #000; }


.close {
    background: #606061;
    color: #FFFFFF;
    line-height: 25px;
    position: absolute;
    left: 5px;
    text-align: center;
    top: 5px;
    width: 24px;
    text-decoration: none;
    font-weight: bold;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    border-radius: 12px; }

.close:hover {
    background: #118899; }
