@font-face {
	font-family: 'Material Icons';
	font-style: normal;
	font-weight: $font-weight-normal;
	src: url(http://www.ti.com/assets/fonts/MaterialIcons-Regular.eot); }

@font-face {
	font-family: 'Material Icons';
	font-style: normal;
	font-weight: $font-weight-normal;
	src: local('Material Icons'), local('MaterialIcons-Regular'), url(http://www.ti.com/assets/fonts/MaterialIcons-Regular.woff2) format('woff2'), url(http://www.ti.com/assets/fonts/MaterialIcons-Regular.woff) format('woff'); }

@font-face {
	font-family: 'ti-icons';
	font-style: normal;
	font-weight: $font-weight-normal;
	src: url(http://www.ti.com/assets/fonts/ti-icons.eot?qm906k); }

@font-face {
	font-family: 'ti-icons';
	font-style: normal;
	font-weight: $font-weight-normal;
	src: url(http://www.ti.com/assets/fonts/ti-icons.woff?qm906k) format('woff'); }

*:before {
	font-family: 'Material Icons';
	font-style: normal;
	font-weight: $font-weight-normal;
	font-size: 1em;
	display: inline-block;
	line-height: 1;
	text-transform: none;
	letter-spacing: normal;
	word-wrap: normal;
	-webkit-font-smoothing: antialiased /* Support for all WebKit browsers. */;
	text-rendering: optimizeLegibility /* Support for Safari and Chrome. */;
	-moz-osx-font-smoothing: grayscale /* Support for Firefox. */;
	font-feature-settings: 'liga' /* Support for IE. */;
	float: left; }
