.info-pane input[type="checkbox"] {
  display: none;

  + label::before {
    content: '\e88e';
    color: #ccc;
    display: inline-block;
    font-size: 1.5em;
    cursor: pointer;
    margin-left: 0.1em; }

  &:checked {
    + label::before {
      color: #999; }
    + label + .pane {
      opacity: 1;
      display: block;
      max-height: 1200px;
      background: white;
      border: 1px solid #CCC;
      padding: 1em 1.8em 1em 1em;
      border-radius: .2em;
      overflow: hidden;
      margin: 1em 0;
      -webkit-transition: max-height .2s, margin .2s, opacity .2s;
      transition: max-height .2s, margin .2s, opacity .2s; } }

  + label + .pane {
    display: block;
    max-height: 0px;
    overflow: hidden;
    max-width: 500px;
    margin: 0;
    opacity: 0;
    -webkit-transition: max-height .2s, margin .2s, opacity .2s;
    transition: max-height .2s, margin .2s, opacity .2s; } }

.info-pane {

  &.floating input[type="checkbox"] {

    + label::before {
      content: ''; }

    + label {
      color: $color-link;
      cursor: pointer; }

    + label + .pane {
      position: absolute;
      max-width: 400px;
      width: 150%;
      -webkit-transition: opacity .2s;
      transition: opacity .2s; }

    &:checked {
      + label {
        color: #156; }
      + label + .pane {
        opacity: 1;
        display: block;
        position: absolute;
        max-height: 1200px;
        background: white;
        border: none;
        box-shadow: 0 10px 22px rgba(0, 0, 0, 0.38);
        padding: 1em 1.8em 1em 1em;
        border-radius: 0em;
        overflow: hidden;
        margin: 1em 0;
        -webkit-transition: opacity .2s;
        transition: opacity .2s; } } } }

