.filter-buttons {
	display: inline-block;
	width: 100%;
	border-bottom: 1px dotted $color-body;

	button {
		white-space: normal;
		color: $color-teal5;
		text-align: left;
		padding-right: 2.3em;
		&:hover {
			border: 1px solid $color-grey3;
			color: $color-grey3; }
		&.close:before {
			content: "\E5CD";
			color: $color-body;
			font-size: 1.2em;
			margin: .1em -1.5em -.5em 0;
			padding: 0 0 0 .75em;
			float: right; }
		&.close:hover:before {
			color: $color-red4; }
		&.more:before {
			content: "\E5CC";
			float: right;
			font-size: 1.3em; } }
	ul {
		margin-left: 0;
		li {
			background: none;
			margin-left: 0;
			float: left; }
		li ul li {
			background: rgba(0, 0, 0, 0) url(/assets/images/hierarchy-indicator.png) no-repeat scroll .5em -.25em;
			padding-left: 1.5em;
			margin-top: -.25em;
			padding-top: 0; } } }
