
@import url(/assets/css/ticom3-common.css);

#ls-canvas {
	min-width: 1000px;

	a.silo-title {
		display: inline-block;
		font-size: 1.6em;
		padding-bottom: 2em;
		color: $color-title; }

	.silotitle a {
		color: $color-title; }

	.row {
		max-width: 1000px; } }

#ls-row-1 {
	.breadcrumbs {
		padding: 0 $page-margin-ie;
		padding: 0 $page-margin {

			font-weight: $font-weight-bold; } } }

#ls-row-3-col-2, #ls-row-3-area-2 {
	float: left;
	width: 80%;
	/*max-width: 1000px*/
	padding: 0 $page-margin-ie;
	padding: 0 $page-margin; }

#ls-gen5-ls-lqr > #ls-row-3-col-1, #ls-gen3-ls-lqr > #ls-row-2-area-1 {
	padding: 1em $page-margin-ie;
	padding: 1em $page-margin; }

#ls-row-3-col-2-row-2-col-1-row-1-area-1 {
	float: left;
	width: 60%; }
#ls-row-3-col-2-row-2-col-1-row-1-area-2 {
	float: left;
	width: 40%;
	padding: 0 0 0 2em; }

#ls-canvas.collapsed-animated {
	#ls-gen7-ls-lqr-w-fx {
		position: absolute;
		animation: collapsed-animated-left 1s 1s forwards; }

	#ls-row-3-col-2, #ls-row-3-area-2 {
		margin-left: 20%;
		width: 80%;
		animation: collapsed-animated-right 1s 1s forwards; } }

@keyframes collapsed-animated-left {
	0% {
		left: 0%; }
	100% {
		left: -25%; } }

@keyframes collapsed-animated-right {
	100% {
		width: 100%;
		margin: 0;
		padding-left: 4em;
		height: auto; } }

// applications
#ls-gen7-ls-lqr-w-fx {
	table {
		width: 100%; } }

#flyout_menu {
	input.filter {
		width: 100%;
		background: #fff url(/js/shared/apps/img/filter_passive.gif) no-repeat 98% 50%; }

	.result-count {
		position: relative;
		margin: 1em 0;

		a#fl_resetLink {
			position: absolute;
			right: 0;
			bottom: 0;
			color: #cc0000; } }

	ul ul {
		display: none; }

	ul.active {
		display: block; }

	#flyList > ul > li > a:not(.appcategindent) {
		font-weight: $font-weight-bold; }

	span.highlight {
		background: #ff0; } }

.appsMegaFlyout, #appsMegaFlyoutNotch {
	display: none !important; }

#tab {
	position: relative;
	width: 100%;
	border-bottom: 1px solid $color-body;

	a {
		color: $color-title !important;
		text-decoration: none;
		cursor: pointer; }

	ul:before, ul:after {
		content: "";
		display: table; }

	ul:after {
		clear: both; }

	ul {
		margin: 0;
		padding: 0 $page-margin-ie;
		padding: 0 $page-margin;
		display: flex;
		align-items: stretch; }

	li {
		background: url(/assets/images/iconTab.png) no-repeat 0 0;
		position: relative;
		float: left;
		margin: 0 1.4em 0 -.7em;
		padding: .7em .1em .7em .7em;
		color: $color-grey4;

		&:after {
			background: url(/assets/images/iconTab.png) no-repeat right 0;
			position: absolute;
			top: 0;
			right: -24px;
			width: 25px;
			height: 100%;
			content: ""; }

		&#active {
			font-weight: $font-weight-bold;
			background-position: 0 -120px;
			z-index: 1;

			a {
				color: $color-white !important; }

			&:after {
				background-position: right -120px; } } }

	li, li:after {
		background-image: url(/assets/images/iconTab.svg), none; } }
