$font-family-body: 'Open Sans', sans-serif;
$font-family-title: 'Roboto', sans-serif;

$color-body: #555;

$color-title: #333;

$color-link: #189;

$color-link-visited: #a66;
$color-link-hover: #c00;

$color-white: #fff;
$color-black: #000;

$color-grey1: #f9f9f9;
$color-grey2: #f0f0f0;
$color-grey3: #e0e0e0;
$color-grey4: #c0c0c0;
$color-grey5: #b0b0b0;
$color-grey6: #555;

$color-red1: #fbeeee;
$color-red2: #f4dfdf;
$color-red3: #efb9b9;
$color-red4: #c00;
$color-red5: #900;

$color-teal1: #edf7f8;
$color-teal5: #189;
$color-teal6: #156;

$color-yellow1: #ffffe5;
$color-yellow2: #ffffb3;

$color-error: #c00;
$color-success: #096;

$font-weight-normal: 400;
$font-weight-bold: 600;

$margin: 2em;
$page-margin: 2rem;
$page-margin-ie: 2em;

@mixin column-count($count) {
	-webkit-column-count: $count;
	-moz-column-count: $count;
	column-count: $count; }

@mixin column-gap($gap) {
	-webkit-column-gap: $gap;
	-moz-column-gap: $gap;
	column-gap: $gap; }
