.alert-message {
	margin: 2em 0;
	padding: 1em;
	background-color: $color-teal1;
	border: 1px solid $color-teal5;
	border-radius: 5px;
	p, ul {
		margin: 0.25em 0 0 6.2em; }
	ol {
		margin: 0.25em 0 0 7.5em; }
	h3 {
		margin: 0.2em 1em 0 0; }
	&:before {
		font-size: 5em;
		color: $color-teal5;
		padding: 0 .25em 0 0;
		margin: 0 -1em 100% 0;
		content: "\e031";
		margin: 0; }
	&.info {
		&:before {
			content: "\e88e"; } }
	&.tips {
		&:before {
			content: "\e85D"; } }
	&.undo {
		&:before {
			content: "\e166"; } }
	&.alert {
		background-color: $color-yellow2;
		border: 1px solid $color-grey6;
		&:before {
			color: $color-grey6;
			content: "\e002"; } }
	&.form-error, &.error {
		background-color: $color-red1;
		border: 1px solid $color-red5;
		&:before {
			color: $color-red5;
			content: "\e000"; } }
	&.form-success {
		background-color: $color-white;
		border: 1px solid $color-success;
		&:before {
			color: $color-success;
			content: "\e86c"; } }

	.close {
		border-radius: 50%;
		cursor: pointer;
		font-size: 1.3em;
		padding: .2em;
		margin: -.5em -.5em 0 0;
		float: right;
		&:hover {
			background: rgba(150, 150, 150, 0.3); }
		&::before {
			content: "\e5cd"; } } }

.alert-input {
	display: none;
	&:checked {
		+ .alert-message {
			opacity: 0;
			max-height: 1px;
			padding: 0;
			margin: 0;
			transition: opacity .1s, padding .1s, margin .1s;
			&:before {
				max-height: 0; } } }
	&:not(checked) {
		+ .alert-message {
			opacity: 1;
			transition: opacity .1s, padding .1s, margin .1s; } } }
