.collapsible {
	.trigger, .click {
		cursor: pointer;

		&:before {
			background: $color-teal5;
			border-radius: 50%;
			color: $color-white;
			font-size: 1em;
			margin: .2em .5em 0 0;
			content: '\e5ce'; } }

	.content, .collapsed {
		display: none; }

	&.collapsed {
		.trigger {
			&:before {
				content: '\e5cf'; } } }

	&.divided:not(.collapsed) {
		border: 1px solid $color-grey3;
		border-width: 1px 0;
		margin: 1em 0;
		padding: 1em 0; }

	&.boxed {
		background: $color-grey2;
		border: 1px solid $color-grey3;
		margin-bottom: -1px;

		.trigger {
			padding: 1em;
			margin-bottom: 0;

			&:before {
				background-color: transparent;
				color: $color-grey5;
				font-size: 1.2em;
				margin: .1em -.3em 0 0;
				float: right; } }

		.content {
			padding: 0 1em; } }

	&.text {
		.trigger {
			position: relative;
			width: 100%;
			height: 3em;
			text-align: center;
			border-bottom: 1px solid $color-grey3;
			margin: -3em 0 1em;
			margin-top: -2.5em\9;


			&:before {
				position: absolute;
				bottom: -.5em;
				bottom: -.3em\9;
				background: $color-white;
				padding: 0 .5em;
				padding-left: 0\9;
				color: $color-link;
				font-family: 'Open Sans';
				content: 'less'; } }

		&.collapsed {
			.trigger {
				background: none;
				background: -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
				background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
				background: -ms-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff', GradientType=0 );

				&:before {
					content: 'more'; } } } } }

.tblstandard {
	.collapsible {
		.trigger {
			&:before {
				background: $color-white;
				color: $color-link;
				font-family: 'Open Sans';
				font-size: .9em;
				content: 'less';
				margin: .5em 0 0 0; } }

		&.collapsed {
			.trigger {
				&:before {
					content: '... more'; } } }

		.click {
			&:before {
				background: $color-white;
				color: $color-link;
				font-family: 'Open Sans';
				content: '...'; } } } }



$iconexpanded:  '\e5ce';
$iconcollapsed: '\e5cf';

.collapsible { // default collapsible styles

	.collapsible-item {
		border: 1px solid transparent;
		border-width: 1px 0;

		.collapsible-item-trigger {
			clear: both;
			margin: 0;
			padding: .8em .8em;
			cursor: pointer;
			transition: background 500ms;
			background: none;

			&:before {
				width: 1em;
				margin: .2em .6em 0 0;
				content: $iconcollapsed;
				text-align: center;
				color: white;
				border-radius: 4px;
				background: #189;
				font-size: 1em; }

			&:hover {
				background: #f9f9f9; } }

		.collapsible-item-content {
			display: none;
			margin: .8em; }

		h2, h3 {
			font-size: 1.2em; }

		h4, h5 {
			font-size: 1.0em; } }

	.collapsible-item.is-expanded {
		border: 1px dotted #c0c0c0;
		border-width: 1px 0;

		.collapsible-item-trigger {
			background: none;

			&:before {
				content: $iconexpanded;
				background: #c00; }

			&:hover {
				background: none; } } } }

.collapsible.mod-collapsible-bg { // collapsible modifier - background highlight
	.collapsible-item {
		.collapsible-item-trigger {
			background: #f9f9f9;
			&:hover {
				background: #eee; } } }
	.collapsible-item.is-expanded {
		.collapsible-item-trigger {
			background: transparent; } } }

.collapsible.mod-collapsible-toolbox { // collapsible modifier - toolbox

	.collapsible-item {
		.collapsible-item-trigger {
			background: #f9f9f9;

			&:before {
				float: right;
				margin: .3em .2em 0 0;
				transition: color 500ms;
				color: #b0b0b0;
				background: transparent; }

			&:hover {
				background: #eee; } }

		ul.collapsible-item-content {
			margin: 0 0 -1px 0;
			padding: 0;

			li {
				padding: .6em .8em; } }

		div.collapsible-item-content {
			margin: .8em;
			padding: 0; } }

	.collapsible-item.is-expanded {
		border-color: transparent;

		.collapsible-item-trigger {
			background: #f9f9f9;

			&:before {
				background: transparent; }

			&:hover {
				background: #eee; } } } }

.collapsible.mod-collapsible-text { // collapsible modifier - collapsible text

	.collapsible-item {
		.collapsible-item-trigger {
			position: relative;
			width: 100%;
			height: 4em;
			margin: -3.5em 0 1em 0;
			padding: 0;
			cursor: pointer;
			transition: background 500ms;
			border-bottom: 1px solid #e0e0e0;
			background: none;
			background:    -moz-linear-gradient(top, rgba(255, 255, 255, 0) 0%, white 100%);
			background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, white 100%);
			background:     -ms-linear-gradient(top, rgba(255, 255, 255, 0) 0%, white 100%);
			background:         linear-gradient(top, rgba(255, 255, 255, 0) 0%, white 100%);
			filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ffffff', endColorstr='#ffffff', GradientType=0 );

			&:before {
				position: absolute;
				bottom: -.5em;
				left: 50%;
				display: block;
				width: auto;
				margin: 0 0 0 -1em;
				padding: 0 0 0 .4em;
				background: #fff;
				font-family: 'Material Icons';
				content: $iconcollapsed;
				color: #189;
				z-index: 3; }

			span {
				display: inline-block;
				position: relative;
				top: 3.2em;
				left: 50%;
				padding: 0 .6em;
				background-color: #fff;
				z-index: 2;
				color: #189; }

			&:hover {
				border-color: #c0c0c0;

				&:before, span {
					color: #c00; } }

			.collapsible-item-trigger-less {
				display: none; }

			.collapsible-item-trigger-more {
				display: inline-block; } }

		.collapsible-item-content {
			margin: 0; }

		p:last-child:after {
			display: inline-block;
			content: '..';
			transition: opacity 300ms; } }

	.collapsible-item.is-expanded {
		border-color: transparent;

		.collapsible-item-trigger {
			background: none;
			filter: initial;

			.collapsible-item-trigger-less {
				display: inline-block; }

			.collapsible-item-trigger-more {
				display: none; }

			&:before {
				content: $iconexpanded;
				color: #189;
				background: #fff; }

			&:hover {
				background: none;
				filter: initial;

				&:before {
					color: #c00; } } }

		p:last-child:after {
			opacity: 0; }

		.collapsible-item-content {} }

	&.mod-collapsible-textsmall { // collapsible text modifier - smaller area
		.collapsible-item {
			.collapsible-item-trigger {
				border-bottom: 0;

				span {
					left: 0;
					padding: 0 0 0 1.2em; }

				&:before {
					left: 0;
					bottom: -.4em;
					padding: 0;
					margin: 0; } } } } }
