.prod-banner {
	position: relative;
	font-size: 0;
	line-height: 0;
	max-width: 602px;
	&.square {
		position: relative;
		font-size: 0;
		line-height: 0;
		max-width: 302px !important;
		min-width: 200px !important; }
	&.no-border {
		border: none;
		max-width: 600px; }
	&.center {
		margin: 0 auto; }
	&.right {
		margin-left: auto;
		margin-right: 0; }
	&.no-border {
		&.square {
			border: none;
			max-width: 300px; }
		.slides {
			border: none; } }
	* {
		padding: 0;
		/* margin:0; */ }
	img {
		max-width: 100%; }
	.slides {
		position: relative;
		border: 1px solid $color-grey4;
		> ul {
			position: absolute;
			overflow: hidden;
			white-space: nowrap;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			z-index: 1;
			> li {
				position: absolute;
				display: block;
				width: 100%;
				max-width: 100%;
				line-height: 0;
				top: 0;
				left: 100%;
				bottom: 0; } }
		li.selected {
			left: 0;
			z-index: 1; }
		&.fade li {
			left: 0 !important;
			right: 0 !important; }
		a {
			display: block;
			position: relative;
			img {
				border: 0; } }
		.position {
			position: static;
			display: block;
			opacity: 0;
			-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
			filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
			img {
				opacity: 0;
				z-index: 0; } }
		> .arrow {
			position: absolute;
			width: 80px;
			top: 0;
			bottom: 0;
			text-indent: -99999em;
			outline: none;
			cursor: pointer;
			opacity: 0;
			z-index: 5;
			-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
			filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0); }
		.arrow {
			&.next {
				background: url(/assets/images/slideNext.png) center right no-repeat;
				right: 0; }
			&.prev {
				background: url(/assets/images/slidePrev.png) center left no-repeat;
				left: 0; } } }
	.prod-control {
		position: relative;
		width: 100%;
		background: url(/assets/images/slideBg.png) no-repeat;
		background-size: 100% 100%;
		height: 22px;
		ul {
			position: absolute;
			width: 100%;
			height: 100%;
			bottom: 0px;
			text-align: center;
			li {
				margin: 5px 2px;
				display: inline-block;
				zoom: 1;
				background: url(/assets/images/slideNav_off.png) no-repeat;
				background/*\**/: url(/assets/images/slideNav_off_low.png) no-repeat\9;
				/* hack for IE8 */
				background-size: 100% 100%;
				text-indent: -99999em !important;
				width: 9px;
				height: 9px;
				cursor: pointer;
				&.selected {
					background: url(/assets/images/slideNav_on.png) no-repeat;
					background/*\**/: url(/assets/images/slideNav_on_low.png) no-repeat\9;
					/* hack for IE8 */
					background-size: 100% 100%; } } } } }

/* for Resposive Banner */

.isSmallScreen {
	display: none; }

@media (max-width: 639px) {
	.mobile .isSmallScreen {
		display: inline-block;
		content: "";
		width: 1px;
		height: 1px;
		overflow: hidden; } }
