.drawer {
	input[type=search] {
		background: $color-red4 url(/assets/images/iconHeader.svg) no-repeat 13px -385px;
		border: 0;
		height: 44px;
		width: 100%;
		padding-left: 3em;
		color: $color-white; } }

header.drawer {
	display: none;
	position: fixed;
	left: 0;
	right: 0;
	background-color: $color-red4;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
	z-index: 10001;

	input[type=search] {
		background: $color-red4 url(/assets/images/iconHeader.svg) no-repeat 13px -385px;
		border: 0;
		height: 44px;
		width: 100%;
		padding-left: 3em;
		color: $color-white;
		display: none;
		position: absolute; }

	input.active[type=search] {
		display: block; }

	button {
		border: 0;
		text-indent: -9999px;
		height: 44px;
		width: 44px;
		float: left;
		cursor: pointer;
		/*z-index: 2*/

		&.search {
			background: url(/assets/images/iconHeader.svg) no-repeat 13px -385px;
			float: right; }

		&.drawer {
			background: url(/assets/images/iconHeader.svg) no-repeat 13px -1285px; } } }

nav.drawer {
	float: left;
	width: 25%;
	/*max-width: 300px*/
	background: $color-white;
	border-right: 1px solid $color-grey3;
	z-index: 10003;

	form {
		position: relative;
		margin: .5em 0; }

	a/*, a:visited*/ {
		display: block;
		color: $color-title; }

	input[type=search] {
		background-position: 0 -380px;
		border-bottom: 1px solid $color-white;
		height: 40px;
		padding: 1em 0 0 2em;
		border-radius: 0; }

	h3 {
		margin-bottom: 0; }

	ul {
		margin-left: 0; }

	li {
		position: relative;
		background: none;
		margin: 0;
		border-top: 1px solid $color-grey3;
		padding: 0;

		li {
			border-top-style: dashed; }

		&.active > a {
			color: $color-red4;
			font-weight: $font-weight-bold; } }

	.head {
		background: $color-white;
		padding: 1em;
		margin-bottom: 1em;

		a {
			background: url(/assets/images/iconHeader.svg) no-repeat 0 0 / 100% auto;
			width: 260px;
			height: 30px; } }

	.nav {
		background: $color-white;
		margin-bottom: 2em;

		a {
			padding: .8em $page-margin-ie;
			padding: .8em $page-margin; }

		> ul {
			border-bottom: 1px solid $color-grey3; }

		li ul {
			display: none; }

		li.expanded ul {
			display: block; }

		li li li a {
			padding-left: 3em; }

		a.icon {
			position: absolute;
			top: 0;
			right: 0;
			width: 3em;
			height: 3em;
			background: url(/assets/images/iconHeader.png?versionNumber=1.1) no-repeat 1em -1185px;
			background-image: url(/assets/images/iconHeader.svg?versionNumber=1.1), none;
			padding: 0;
			cursor: pointer; } }

	.foot {
		a {
			padding: .8em 0; }

		li {
			float: left;
			border: 0; }

		&.navHeader {
			background: $color-red4;
			padding: 1em;

			a, a:visited {
				color: $color-white; }

			li {
				width: 50%; } }

		&.navFooter {
			padding: .5em 1em;

			a {
				color: $color-grey5; }
			li {
				margin-right: 1.5em;

				&:first-child {
					width: 100%; } } } } }

main.content {
	float: left;
	width: 75%;
	padding: 2em;
	-webkit-transition: padding-left 0.2s ease-in;
	-moz-transition: padding-left 0.2s ease-in;
	transition: padding-left 0.2s ease-in; }

.modalMask {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.5);
	opacity: 0;
	visibility: hidden;
	z-index: 10002; }

.drawerOpen {
	.modalMask {
		opacity: 1;
		visibility: visible; } }
