
a {
	color: $color-link;
	text-decoration: none;
	outline: 0;

	&:link:hover {
		text-decoration: underline; } }

/*	&:visited
		color: $color-link-visited*/

a.more {
	background: url(/assets/images/iconMore.png) no-repeat 100% 60%;
	background-image: url(/assets/images/iconMore.svg), none;
	padding: 0 12px 0 0; }

a.cta-red-cir {
	background: url(/assets/images/cta-red-cir.png) no-repeat 0 .2em;
	background-image: url(/assets/images/cta-red-cir.svg), none;
	padding: 0 0 0 1.3em; }

.button {
	display: inline-block;
	font-size: 1em;
	padding: .3em .6em;
	border-radius: .2em;
	border: 0;
	white-space: nowrap;
	cursor: pointer;
	-webkit-appearance: none;
	background-color: $color-grey1;
	border: 1px solid $color-grey4;

	&:link:hover {
		text-decoration: none; }

	&.btnD {
		&.arrow {
			position: relative;
			padding-right: 1.2em;

			span {
				position: absolute;
				top: 0.6em;
				right: 0.3em;
				width: 8px;
				height: 12px;
				background: url(/assets/images/iconMore.png) no-repeat 0 0;
				background-image: url(/assets/images/iconMore.svg), none; } } }

	&.btnA, &.red, &.btnB, &.black, &.btnC, &.teal {
		font-weight: $font-weight-bold;
		color: $color-white !important;
		background-color: $color-red4;
		border: 0 /* legacy style */;

		&:hover {
			background-color: #dd1111; } }

	&.btnB, &.black {
		background-color: $color-title;

		&:hover {
			background-color: #444; } }

	&.btnC, &.teal {
		background-color: $color-teal6;

		&:hover {
			background-color: #067; } }

	&.disabled, &:disabled, &:disabled:hover {
		color: $color-white;
		background-color: $color-grey3;
		cursor: default; }

	span /* old format */ {
		&.pdf, &.download, &.enlarge {
			padding: .1em 0 0 1.8em;
			display: inline-block;
			background: url(/assets/images/icons/icon_m_pdf_btn_c.png) no-repeat 0 0; }

		&.download {
			background-image: url(/assets/images/icons/icon_m_doc-download_btn_c.png); }

		&.enlarge {
			background-image: url(/assets/images/icons/icon_m_zoomin_a.png); } }

	&.pdf, &.download {
		background: $color-teal6 url(/assets/images/icons/icon_m_pdf_btn_c.png) no-repeat .4em .4em;
		padding: .4em .8em .4em 2.2em;

		span {
			display: block;
			color: #88b8bd;
			font-size: .7em;
			font-weight: $font-weight-normal;
			background: none; } }

	&.download {
		background-image: url(/assets/images/icons/icon_m_doc-download_btn_c.png); }

	&.lrg {
		padding: .75em 1.75em; }

	&.prevNext {
		position: relative;
		color: $color-white;
		background: url(/assets/images/iconPrevNext.png) no-repeat right 0;
		background-image: url(/assets/images/iconPrevNext.svg), none;
		height: 28px;
		padding: .3em .6em .3em 0;
		border-radius: 0;

		&:after {
			position: absolute;
			top: 0;
			background-position: 0 0;
			width: 11px;
			height: 28px;
			content: ""; }

		&.prev {
			&:after {
				left: -11px; } }

		&.next {
			background-position: 0 -100px;
			padding: .3em 0 .3em .6em;

			&:after {
				background-position: right -100px;
				right: -11px; } } }

	&.noborder {
		@extend a.more;
		border: 0 /* legacy style */; }

	& + .button {
		margin-left: 1em; } }

.btn1, .btn3 {
	display: inline-block;
	font-size: 1em;
	padding: .3em .6em;
	border-radius: .2em;
	border: 0;
	white-space: nowrap;
	cursor: pointer;
	-webkit-appearance: none;
	font-weight: $font-weight-bold;
	color: $color-white !important;
	background-color: $color-red4;

	&:hover {
		text-decoration: none;
		background-color: #dd1111; } }

.btn3 {
	background-color: $color-body;

	&:hover {
		background-color: #444; } }

.button-zoom {
	cursor: pointer;
	overflow: hidden;
	position: relative;
	display: inline-block;

	&:before {
		background: rgba(0,0,0,0.75);
		top: 50%;
		left: 50%;
		font-size: 3em;
		color: #FFF;
		border-radius: .2em;
		padding: .1em;
		margin: -25px 0 0 -25px;
		z-index: 1;
		position: absolute;
		content: "\e8b6"; }

	&:hover {
		&::before {
			content: "\e8ff"; } } }

