/* View more button */
.showMore .showContent, .ls-canvas .cnt.less, #bottomcontent .cnt.less {
	max-height: 200px;
	overflow: hidden; }

.pageTitle2 .showMore .showContent /* for product folder */ {
	max-height: 70px; }

.showMore.showLess .showContent {
	max-height: none; }

.showMore .showButton, .cnt.less ~ .showMore, .showMore ~ .showLess {
	font-size: .9em;
	border-top: 1px solid $color-grey3;
	text-align: center;
	height: 1px;
	margin: 1em 0 0 0; }

.showMore .showButton span, .cnt.less ~ .showMore a, .showMore ~ .showLess a {
	position: relative;
	top: -.8em;
	background: $color-white;
	color: $color-link;
	padding: 0 .5em;
	border-radius: .2em;
	cursor: pointer;
	z-index: 1; }
