body, input, select, textarea, table, button {
	font-family: $font-family-body; }

body {
	font-weight: $font-weight-normal;
	font-size: 14px;
	color: $color-body;
	margin: 0;
	padding: 0;
	/*min-width: 1000px*/ }

* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	-ms-box-sizing: border-box;
	box-sizing: border-box; }

.hidden {
	display: none; }

.nowrap {
	white-space: nowrap; }

.clear {
	&:before, &:after {
		content: "";
		display: table; }

	&:after {
		clear: both; }

	clear: both;
	zoom: 1; }

.row, header > div, .tab, .anchor, footer {
	@extend .clear; }

img {
	border: 0; }

b, strong {
	font-weight: $font-weight-bold; }

h1, h2, h3, h4, h5, h6 {
	font-weight: $font-weight-bold;
	margin: 0 0 .5em 0;
	color: $color-title; }

h1 {
	font-size: 2em; }
h2 {
	font-size: 1.6em; }
h3 {
	font-size: 1.4em;
	position: relative; }
h4 {
	font-size: 1.2em; }
h5 {
	font-size: 1em; }
h6 {
	font-size: 1em; }
p {
	margin: 0 0 1em 0;
	max-width: 60em; }

ul {
	list-style-type: none;
	padding: 0;
	margin: 0 0 1em 1em;

	&.no-style {
		margin: 0;
		> li {
			padding: 0;
			background: none; } }

	> li {
		list-style-type: none;
		background: url(/assets/images/ulCircle.png) no-repeat 0 .6em;
		padding-left: .6em;
		margin: 0 0 .5em 0;

		ul {
			li {
				background: url(/assets/images/ulDash.gif) no-repeat 0 .7em;
 } } } }				// padding-left: .5em

ol {
	padding: 0;
	margin: 0 0 1em 2.3em;

	> li {
		margin: 0 0 .5em 0; } }

ul ul, ol ul, ul ol, ol ol {
	margin-top: .5em;
	margin-bottom: 0; }

sup, sub {
	position: relative;
	font-size: .7em;
	top: -.3em;
	vertical-align: baseline;
	font-weight: 300; }
sub {
	top: .3em; }

hr {
	border: 0;
	border-bottom: 1px dotted $color-grey4;
	margin-bottom: 1em; }
