.overviewHeader {
	background: url(/assets/images/valuePropHeaders/22244_generic_bkgd.jpg) no-repeat 0 0;
	height: 163px;
	z-index: 0;
	max-width: 943px;

	.overviewHeaderFade {
		background: url(/assets/images/valuePropHeaders/overviewHeader-fg.png) no-repeat 0 0;
		display: block;
		height: 163px;
		background-size: 100% 163px;
		position: relative;
		z-index: 0;
		padding-top: 2em; }

	.valprop-header, h1 {
		font-size: 2.2em;
		font-weight: 200;
		padding-top: 10px;
		color: $color-title;
		margin-bottom: 0.5em;

		span {
			font-weight: bold;
			color: $color-red4; } }

	.valprop-sub-header {
		font-weight: 200;
		padding-right: 1em;
		color: $color-body;
		font-size: 1.3em; }

	.c8, .c4, .c12 {
		z-index: 1; }

	.bracket {
		background: url(/assets/images/valuePropHeaders/bracket-divider.png) no-repeat 0 0;
		display: block;
		height: 71px;
		position: relative;
		width: 70px;
		z-index: 100;
		margin-bottom: -71px;
		left: -20px; }

	.slide, .frame {
		background-image: none;
		max-height: 163px;
		max-width: 277px;
		width: 277px;
		height: 163px; }

	&.slides .slide, &.frames .frame {
		position: absolute;
		opacity: 0; }

	&.automotive {
		background-image: url(/assets/images/valuePropHeaders/22244_automotive_bkgd.jpg); }

	&.audio {
		background-image: url(/assets/images/valuePropHeaders/22244_audio_bkgd.jpg); }

	&.dataConverters {
		background-image: url(/assets/images/valuePropHeaders/22244_data_converter_bkgd.jpg); }

	&.industrialStrength {
		background-image: url(/assets/images/valuePropHeaders/22244_industrial_strength_bkgd.jpg); }

	&.powerMgmt {
		background-image: url(/assets/images/valuePropHeaders/22244_power_mgmt_bkgd.jpg); }

	&.sensingTech {
		background-image: url(/assets/images/valuePropHeaders/22244_sensingt_bkgd.jpg); }

	&.amplifiers {
		background-image: url(/assets/images/valuePropHeaders/22244_amplifiers_bkgd.jpg); }

	.off {
		display: none; }

	.valprop-emphasis {
		color: $color-red4; } }
