.modalAlert-bg {
	z-index: 10002;
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background: $color-black;
	opacity: .60;
	filter: alpha(opacity=60); }

.modalAlert {
	z-index: 10003;
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	display: -webkit-flexbox;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-align: center;
	-ms-flex-align: center;
	-webkit-align-items: center;
	align-items: center;

	.modalWindow {
		position: relative;
		background: $color-white;
		margin: 0 auto;
		min-width: 320px;
		max-width: 94%;
		max-height: 500px;
		overflow: auto;
		padding: 1em 3em 2em 3em;
		border-radius: .2em;
		box-shadow: 0 0 10px $color-black;

		.close {
			margin: -.5em -2.5em;
			position: absolute;
			color: $color-body !important;
			background: url(/assets/images/iconCloseModal.png);
			background-image: url(/assets/images/iconCloseModal.svg), none;
			width: 18px;
			height: 18px;

			&:active {
				background-position: 0 -100px; } }

		.userInfo {
			float: left;
			margin: 1em 0 0 0; } } }
