
.spinner-icon {
	animation: clockwise 0.75s linear infinite;
	border: 3px solid;
	border-color: $color-link $color-link $color-link rgba(255, 255, 255, 0.25);
	border-radius: 100%;
	height: 30px;
	width: 30px;
	background: url(/assets/images/spinner.gif) \9;
	border: none\9;
	height: 60px\9;
	width: 60px\9; }

.spinner-target {
	position: relative;

	.spinner-container {
		background: rgba(255, 255, 255, 0.6) none repeat scroll 0 0;
		bottom: 0;
		height: 100%;
		position: absolute;
		top: 0;
		width: 100%;
		z-index: 10; }

	.spinner {
		margin: 300px auto;
		animation: clockwise 0.75s linear infinite;
		border: 3px solid;
		border-color: $color-link $color-link $color-link rgba(255, 255, 255, 0.25);
		border-radius: 100%;
		height: 30px;
		width: 30px;
		background: url(/assets/images/spinner.gif) \9;
		border: none\9;
		height: 60px\9;
		width: 60px\9;
		* {
			&::before, &::after {
				content: ""; } } } }


@keyframes clockwise {
	100% {
		transform: rotate(360deg) translateZ(0px); } }

@keyframes counter-clockwise {
	100% {
		transform: rotate(-360deg) translateZ(0px); } }
