#entryDesc {
    overflow: auto;
    height: 50px;
    margin: .5em 0 1em; }

.galleryThumbTitle {
    font-size: 0.8em;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 160px; }

.hand img {
    border: 1px solid #c0c0c0; }

.galleryRelatedBox {
    background: #fff;
    border: solid 1px #c0c0c0;
    padding: .5em;
    overflow: auto;
    height: 281px; }

.tagDesc {
    overflow: auto;
    height: 40px;
    margin: .5em 0 1em; }

.durDesc, .viewDesc, .postDesc {
    overflow: auto;
    margin: .5em 0 1em; }

.videoContainer {
    border: 1px solid #c0c0c0; }
