// e.g, http://www.ti.com/lsds/ti/arm/arm_cortex_m_microcontrollers/arm_cortex_m4/overview.page
#Table_01 img {
	display: block;
	max-width: none; }

.stack.level1, #ls-canvas .level1 {
	background: $color-teal6;
	padding: 1em;
	text-align: center;

	h3 {
		margin: 0;
		font-size: 1.2em; }

	&, h3, a {
		color: $color-white !important; } }

.c12 .stack.level1 {
	margin: 1.5em 0;
	font-weight: $font-weight-bold;

	a, a:hover {
		color: $color-white;
		margin: 0 .4em; }

	li.active a {
		color: $color-grey4 !important; }

	ul {
		margin: 0;
		color: $color-grey4; }

	li {
		display: inline-block;
		background: none;
		margin: 0;

		&:before {
			content:  " | "; }

		&:first-child:before {
			content:  " "; } } }

.stack.level2, #ls-canvas .level2 {
	background: $color-grey2;
	padding: 1em;
	vertical-align: middle; }

.stack.level3, #ls-canvas .level3 {
	background: $color-teal1;
	padding: 1em;

	&.prodValProp {
		text-align: center;
		padding: 1.75em;

		ul {
			text-align: left; } } }

.stack.level4, #ls-canvas .level4 {
	background: $color-grey3;
	padding: 1em; }

.nested-box {
	background: $color-white;
	padding: 10px;
	margin-bottom: 20px; }

.featurebox {
	position: relative;
	display: block;
	border-bottom: 1px solid $color-grey4;
	color: $color-body;
	padding: 1.4em;
	margin: 0 0 1em 0;
	background: $color-white;
	background: -moz-linear-gradient(top, $color-white 0%, $color-grey2 100%);
	background: -webkit-linear-gradient(top, $color-white 0%, $color-grey2 100%);
	background: -ms-linear-gradient(top, $color-white 0%, $color-grey2 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#f0f0f0');

	.feature-image {
		max-width: 40%;
		min-width: 80px;
		float: left;

		img {
			max-width: 100%;
			min-width: 80px;
			height: auto;
			text-align: center;
			margin-left: auto;
			margin-right: auto; } }

	.feature-content {
		float: left;
		padding: 0 0 0 1em;
		width: 60%; }

	h3 {
		margin: 0 0 1em;
		padding: 0; }

	h4 {
		font-weight: normal;
		margin: 0 0 1em;
		padding: 0; } }

.bord-top {
	border-top: 2px solid $color-grey5;
	padding-top: .8em; }

.bordered {
	border: 1px solid $color-grey5; }

.borderLeft {
	border-left: 1px dotted $color-grey3;
	padding-left: 1em; }

.divider-primary {
	display: block;
	background: url(/assets/images/primary-divider.png) no-repeat 0 0;
	background-size: 100%;
	height: 24px;
	margin: 1em 0; }

.divider-secondary {
	border-top: 1px dotted $color-grey5;
	margin: 1em 0; }

.padded {
	padding: 1em; }

.getStarted {
	border: 2px solid #009933;
	padding: 20px; }

.row.getStarted h3 {
	margin: 0 0 10px 0px;
	padding: 5px 0 0 75px;
	background: url(/lsds/media/images/shared/getting_started_65x82.gif) no-repeat 0 -10px;
	display: block;
	overflow: visible;
	height: 70px;
	vertical-align: middle; }

.addthis_toolbox {
	margin-bottom: 1em; }

.ui-accordion-content {
	padding: 14px 0 0 0; }

p.intro {
	font-size: 1.2em; }

.disclaim {
	font-size: .9em;
	/*margin: 5px 0*/ }

.accordion {
	div {
		position: relative; }

	h3.ui-accordion-header {
		background: $color-grey1;
		background: -moz-linear-gradient(top, #f2f2f2 0%, #d1d1d1 99%);
		background: -webkit-linear-gradient(top, #f2f2f2 0%, #d1d1d1 99%);
		background: -ms-linear-gradient(top, #f2f2f2 0%, #d1d1d1 99%);
		font-family: 'Open Sans', sans-serif;
		font-size: 1.1em;
		color: $color-link;
		border-top: 1px #bbb solid;
		border-bottom: 1px #bbb solid;
		cursor: pointer;
		margin-bottom: -1px;
		text-indent: 54px;
		z-index: 2;
		position: relative;
		line-height: 34px;
		overflow: hidden;

		&:hover {
			background: $color-grey3;
			background: -moz-linear-gradient(top, $color-white 0%, $color-grey3 100%);
			background: -webkit-linear-gradient(top, $color-white 0%, $color-grey3 100%);
			background: -ms-linear-gradient(top, $color-white 0%, $color-grey3 100%);
			border-top: 1px #999 solid;
			border-bottom: 1px #999 solid;
			z-index: 3; }

		&:before {
			content: '+';
			position: absolute;
			display: inline-block;
			left: 0;
			height: 36px;
			line-height: 36px;
			width: 34px;
			text-align: center;
			font-size: 1.3em;
			color: $color-white;
			text-indent: 0;
			background: #d8d8d8;
			background: -moz-linear-gradient(top, #d8d8d8 1%, #b2b2b2 100%);
			background: -webkit-linear-gradient(top, #d8d8d8 1%, #b2b2b2 100%);
			background: -ms-linear-gradient(top, #d8d8d8 1%, #b2b2b2 100%); }

		&:hover:before {
			background: #b2b2b2;
			background: -moz-linear-gradient(top, #b2b2b2 0%, #7f7f7f 100%);
			background: -webkit-linear-gradient(top, #b2b2b2 0%, #7f7f7f 100%);
			background: -ms-linear-gradient(top, #b2b2b2 0%, #7f7f7f 100%); } }

	h3.ui-state-active:before {
		content: '\2013'; }

	h3.ui-accordion-header.last, h3.ui-accordion-header.ui-state-active {
		border-bottom: 1px #bbb solid; }

	h3.ui-accordion-header.last:hover, h3.ui-accordion-header.ui-state-active:hover {
		border-bottom: 1px #999 solid; } }

span.flag {
	color: $color-red4;
	border-right: 2px solid $color-grey4;
	margin: 0 5px 0 0;
	padding: 0 5px 0 0;
	font-size: 90%;
	text-transform: uppercase; }

span.tblIndicator {
	background: url(/assets/images/icons/icon_tblindicator_spt.png) no-repeat 0 -10px;
	display: block;
	height: 16px;
	margin: 0 auto;
	width: 16px; }

a span.tblIndicator {
	background: url(/assets/images/icons/icon_tblindicator_spt.png) no-repeat 0 -36px;
	text-decoration: none; }

a:hover span.tblIndicator {
	background: url(/assets/images/icons/icon_tblindicator_spt.png) no-repeat 0 -62px;
	text-decoration: none; }

.formLine {
	float: left; }

.toolTipHelp {
	padding: 1em;
	font-size: 1em;
	float: left;
	display: block;
	left: 10px;
	position: relative;

	p {
		margin: 0; }

	span {
		position: absolute;
		display: block;
		width: 20px;
		height: 14px;
		left: -19px;
		background: url(/assets/images/eStoreIconSet.png) no-repeat 0 0;
		background-image: url(/assets/images/eStoreIconSet.svg), none; }

	&.boxC {
		background-color: $color-teal5;
		color: $color-white;
		border-radius: 0.2em;
		box-shadow: 1px 1px 1px 1px $color-grey4;

		span {
			background-position: -140px -760px; } } }

.same-height {
	display: flex;

	* {
		min-height: 1px; }

	> div {
		display: flex;
		flex-direction: column; }

	.featurebox, .stretch {
		flex: 1;
		-ms-flex-preferred-size: auto; } }

.price {
	span {
		&.value {
			font-size: 1.2em;
			font-weight: $font-weight-bold; } }

	&.special span {
		&.value {
			color: $color-red4; }

		&.label {
			font-size: 1.2em;
			font-weight: $font-weight-bold; } } }
