.row {
	margin: 0 0 2em 0;

	.center {
		text-align: center; }

	.right {
		text-align: right; }

	.flright {
		float: right; }

	.flleft {
		float: left; }

	img {
		max-width: 100%;
		vertical-align: text-bottom; } }

.c1 {
	width: 8.33%; }

.c2 {
	width: 16.66%; }

.c3 {
	width: 25%; }

.c4 {
	width: 33.33%; }

.c5 {
	width: 41.66%; }

.c6 {
	width: 50%; }

.c7 {
	width: 58.33%; }

.c8 {
	width: 66.66%; }

.c9 {
	width: 75%; }

.c10 {
	width: 83.33%; }

.c11 {
	width: 91.66%; }

.c12 {
	width: 100%; }

.c5_odd {
	width: 20%; }

.c7_odd {
	width: 14.285%; }

.c1, .c2, .c3, .c4, .c5, .c6, .c7, .c8, .c9, .c10, .c11, .c12, .c5_odd, .c7_odd {
	position: relative;
	float: left;
	padding: 0 1.4em 0 0;
	min-height: 1px; }

.cEnd {
	padding-right: 0; }
