.ie8 {
	nav.drawer {
		.head, .foot {
			display: none; } } }

.hide-href {
	a {
		display: none; } }

.mobile {
	.hide-href {
		a {
			display: inline; }

		span {
			display: none; } } }

@media screen and (min-width:641px) {
	main.container {
		display: flex;
		align-items: stretch;
		overflow: auto;
		background: $color-white; }

	nav.drawer {
		.head, .foot {
			display: none; } }

	.multi-columns {
		@include column-count(2);
		@include column-gap(2em); } }

@media screen and (min-width:1900px) {
	.multi-columns {
		@include column-count(3); } }

@media screen and (max-width:1024px) {
	body {
		font-size: 11px;
		-webkit-text-size-adjust: 100%; }

	#tiHeader .top ul, #tiHeader .nav ul, #tiHeader .sub, footer#tiFooter, nav.drawer .nav a, .siloTitle, .pageTitle, .breadcrumb, .pageContent, .pageNav, .tab ul, .anchor ul {
		padding-right: $page-margin / 2;
		padding-left: $page-margin / 2; }

	#tiHeader {
		min-width: inherit;

		li#top_search, li#nav_products, li#nav_applications, li#nav_toolsSoftware, li#nav_supportCommunity, li#nav_sampleBuy, li#nav_aboutTI {
			display: none; }

		li#nav_menu, li#nav_search {
			display: block; }

		[id*="_search"] form {
			width: 100%;
			max-width: 300px; }

		.searchInput {
			width: 50%; }

		.searchFlyout {
			width: 100%; }

		li#top_logo {
			a {
				background-size: 70% auto;
				height: 25px; } }

		/*#sub_cart
			.column.border
				padding-top: 1em

			a.button
				margin: 2.5em 0 0 0*/

		#sub_menu_product {
			> ul {
				float: left;
				width: 33%; } }

		#sub_menu {
			li {
				max-width: 180px; } } }

	#tiFooter {
		.noMobile, .social {
			display: none; } } }

@media screen and (max-width:640px) {
	header.drawer {
		display: block; }

	nav.drawer {
		transform: translateX(-281px);
		visibility: hidden;
		height: 0;

		h3 {
			border-top: 1px solid $color-grey3; } }

	main.content {
		width: 100%;
		padding-top: 44px !important; }

	.drawer {
		#tiHeader, #tiFooter {
			display: none; } }

	.drawerOpen {
		nav.drawer {
			width: 85%;
			float: none;
			position: fixed;
			left: 0;
			top: 0;
			bottom: 0;
			visibility: visible;
			height: auto;
			overflow-x: hidden;
			overflow-y: auto;
			-webkit-transform: translateZ(0);
			-moz-transform: translateZ(0);
			-ms-transform: translateZ(0);
			transform: translateZ(0);
			transition: transform 0.2s ease-in 0.1s;
			-webkit-overflow-scrolling: touch; } }

	#tiHeader {
		li#nav_history, li#nav_cart, li#nav_language, li#nav_profile, li#nav_search {
			a {
				text-indent: -100000em;
				width: 4em; }

			a:before {
				margin-left: .2em;
				text-indent: 0; } }

		li#top_login {
			display: none;

			&.active {
				display: block;
				position: absolute;
				top: 8em;
				z-index: 1; } }

		.columnGroup {
			display: block; }

		.column {
			float: none;
			border: 0;
			padding: 0 0 1em 0;

			&.middle {
				padding-top: 0; }

			&.border {
				border-left: 0;
				padding-left: 0; } }

		.sub {
			margin-left: 0;
			padding-bottom: $page-margin / 2; }

		#sub_menu {
			.column {
				float: left;
				width: 50%; } }

		#sub_menu_product {
			> ul {
				width: 100%; } }

		#sub_language {
			h3 {
				display: none; }

			.column:first-child h3 {
				display: block; } }

		#sub_history {
			ul {
				float: none;
				width: 100% !important;
				border: 0 !important;
				padding-left: 0 !important;
				margin-bottom: 1em; } }

		#sub_profile {
			padding-top: 5em; } }

	.noPhone {
		display: none; }

	.row {
		margin-bottom: 0; }

	.c1, .c2, .c3, .c4, .c5, .c6, .c7, .c8, .c9, .c10, .c11, .c12 {
		float: none;
		padding: 0;
		margin-bottom: 2em;
		width: 100%; }

	form.responsive {
		.c1, .c2, .c3, .c4, .c5, .c6, .c7, .c8, .c9, .c10, .c11, .c12 {
			margin-bottom: 0; } }

	.overviewHeader {
		background-image: none !important;
		height: auto;

		.valprop-header {
			font-size: 1em; }

		.overviewHeaderFade, .bracket, .seoFullWidth, h4, .valprop-sub-header, #valPropFrames, #valPropSlides, #valPropStill {
			display: none !important; } }

	.same-height {
		display: block; } }
