.tooltip {

	.tooltip-content {
		position: absolute;
		background: $color-teal5;
		border-radius: .2em;
		color: $color-white;
		font-size: .9em;
		padding: .5em 1em;
		opacity: 0;
		max-width: 500px;
		max-height: 0;
		margin: 0;
		transition: opacity .2s, max-height .2s, margin .2s; }

	a, img {
		+ .tooltip-content {
			margin-top: .5em;
			z-index: -1; } }

	input:focus, textarea:focus, a:hover, img:hover {
		+ .tooltip-content {
			opacity: 1;
			max-height: 200px;
			margin-left: 1.5em;
			z-index: 1; } } }
