#tiHeader {
	position: relative;
	z-index: 10001;
	width: 100%;
	min-width: 1230px;
	background: $color-white;

	ul > li {
		padding: 0;
		background: none; }

	.top, .nav {
		li {
			float: left;
			margin: 0; } }

	.top {
		margin-top: 1em;

		ul {
			padding: 0 $page-margin-ie;
			padding: 0 $page-margin;
			margin-left: 0; } }

	.nav {
		position: relative;
		/*z-index: 1*/
		background: $color-red4;

		ul {
			padding: 0 $page-margin-ie;
			padding: 0 $page-margin;
			margin-left: -1em; }

		li {
			a, a:hover {
				/*position: relative*/
				text-decoration: none;
				padding: .8em 1em;
				display: block;
				color: $color-white;
				font-weight: $font-weight-bold; }

			&.active {
				a {
					background: $color-white;
					color: $color-black; } }

			&.thirdPartyID {
				a, a:hover {
					font-size: 1.6em;
					padding: .2em .7em .3em .5em !important;
					margin: 0 1em 0 0;
					background: url(/assets/images/iconMore2.png) no-repeat right .7em;
					background-image: url(/assets/images/iconMore2.svg), none; } } } }

	.column {
		> ul {
			margin-left: 0; } }

	li#top_logo {
		a {
			background: url(../../images/ic-logo.png) no-repeat 0 0;
			background-image: url(../../images/ic-logo.svg), none;
			width: 280px;
			height: 36px;
			display: block; } }

	li#top_search {
		padding: 0 3em; }

	li#top_login {
		float: right;
		margin: .6em 1em 0 0; }

	li#nav_history, li#nav_cart, li#nav_language, li#nav_profile, li#nav_search {
		float: right;

		a:before {
			color: $color-white;
			font-size: 1.4em;
			margin-right: .2em; }

		&.active {
			a:before {
				color: $color-red4; } } }

	li#nav_history {
		a:before {
			content: "\e889"; }

		&.active {
			a:before {
				content: "\e889 "; } } }

	li#nav_cart {
		a:before {
			content: "\e8cc "; }

		&.active {
			a:before {
				content: "\e8cc"; } } }

	li#nav_language {
		a:before {
			content: "\e894 "; }

		&.active {
			a:before {
				content: "\e894"; } } }

	li#nav_profile {
		a:before {
			font-family: 'ti-icons';
			content: "\e600 "; }

		&.active {
			a:before {
				content: "\e600"; } } }

	li#nav_search {
		a:before {
			content: "\e8b6 "; }

		&.active {
			a:before {
				content: "\e8b6"; } } }

	li#nav_menu, li#nav_search {
		display: none; }

	.columnGroup {
		display: -ms-flexbox;
		display: -webkit-flex;
		display: -moz-flex;
		display: flex;
		-webkit-align-items: stretch;
		-moz-align-items: stretch;
		-ms-align-items: stretch;
		align-items: stretch; }

	.column {
		float: left;
		padding: 0 1em 0 0;

		&.middle {
			padding-top: 2.1em; }

		&.border {
			border-left: 1px dotted $color-grey3;
			padding-left: 1em; } }

	.sub {
		position: relative;
		display: none;
		padding: 1em $page-margin-ie;
		padding: 1em $page-margin;
		border: 1px solid $color-grey3;
		border-top: 0;
		font-size: .9em;
		background: $color-white;
		background: -moz-linear-gradient(top, $color-white 0%, $color-grey1 100%);
		background: -webkit-linear-gradient(top, $color-white 0%, $color-grey1 100%);
		background: -ms-linear-gradient(top, $color-white 0%, $color-grey1 100%);

		&.active {
			display: block; }

		.expand {
			position: absolute;
			bottom: 0;
			width: 2em;
			height: 2em;
			cursor: pointer;

			&:before {
				margin-left: .3em;
				font-size: 1rem;
				content: "\e5cf"; }

			&.left {
				left: 0; }

			&.right {
				right: 0; } }

		&.subExpanded .expand {
			&:before {
				content: "\e5ce"; } }

		h3 {
			font-size: 1.1em;
			color: $color-red4;
			font-weight: $font-weight-bold;

			a/*, a:visited*/ {
				color: $color-red4; } }

		p {
			max-width: 200px; }

		a {
			color: $color-title; } }

	#sub_history {
		height: auto !important;

		.ABtitle {
			font-size: 1.1em;
			color: $color-red4;
			font-weight: $font-weight-bold;
			padding-left: 0;

			span {
				font-size: .7em;
				font-weight: $font-weight-normal;
				margin: 0 0 0 .5em;

				a/*, a:visited*/ {
					color: $color-red4; } } }

		#activityBar-content-normal {
			display: block; }

		#activityBar-content-expanded {
			display: none; }

		&.subExpanded {
			#activityBar-content-normal {
				display: none; }

			#activityBar-content-expanded {
				display: block; }

			.columnGroup {
				height: auto; } }

		ul {
			float: left;
			margin: 0; }

		#ABproduct, #ABproduct-before {
			width: 50%; }
		#ABtechdocs, #ABtechdocs-before {
			width: 30%;
			border-left: 1px dotted $color-grey3;
			padding-left: 1em; }

		#ABsearch, #ABsearch-before {
			width: 20%;
			border-left: 1px dotted $color-grey3;
			padding-left: 1em; }

		.activityBar-promoLine {
			/*position: absolute*/
			/*left: 3%*/
			/*bottom: .5em*/
			/*width: 94%*/
			text-align: center;
			border-top: 1px dotted $color-grey3;
			padding-top: .5em;
			/*margin: .5em 0 -.5em 0*/
			font-weight: $font-weight-bold; }

		.ABPDpin, .ABPDtrash {
			display: inline-block;
			width: 2em;
			height: 1.4em;

			&:before {
				color: $color-grey5;
				margin: .2em 0 0 .5em; }

			&:hover {
				text-decoration: none; } }

		.ABPDpin {
			&:before {
				font-family: 'ti-icons';
				content: "\e601"; } }

		.ABPDpin.pinned {
			&:before {
				color: $color-link; } }

		.ABPDpin.disabled {
			cursor: auto;

			&:before {
				color: $color-grey2; } }

		.ABPDtrash {
			margin-right: .5em;

			&:before {
				content: "\e14c"; } }

		.totalPinnedItems {
			font-size: .8em;
			padding-left: 0; }

		.ABPDseparator {
			border-top: 1px dotted $color-grey3;
			height: 1px; } }

	#sub_applications {
		.column {
			width: 16.5%; } }

	ul.cartItem {
		li {
			position: relative;
			padding-right: 5em;
			border-bottom: 1px dotted $color-grey3;
			margin-bottom: 0;
			line-height: 1.5; } }

	#sub_cart {
		ul.cartItem {
			span {
				position: absolute;
				right: 0; } }

		/*a.button
			margin: 2.5em 6em 0 0*/ }

	.count {
		position: relative;
		display: inline-block;
		background: $color-red4;
		border-radius: 1em;
		color: $color-white;
		font-size: .7em;
		font-weight: $font-weight-bold;
		width: 1.5em;
		height: 1.5em;
		text-align: center;
		left: .2em;
		top: -.2em;
		line-height: 1.5; }

	[id*="_search"] form {
		position: relative;
		display: inline-block;
		/*z-index: 2*/
		border: 1px solid $color-grey4;
		border-radius: .2em;

		&:before {
			float: right;
			font-size: 1.2em;
			margin: .5em .2em;
			content: "\e8b6"; } }

	.searchFilterLable {
		display: inline-block;
		padding: .5em;
		border-radius: .2em 0 0 .2em;
		border-right: 1px solid $color-grey4;
		background: $color-grey1;
		cursor: pointer;

		&:before {
			float: right;
			margin: .2em 0 0 .2em;
			content: "\e5cf"; } }

	.searchInput {
		padding: .5em;
		border: 0 !important;
		width: 30em;
		background: none; }

	.searchButton {
		position: absolute;
		top: 0;
		right: 0;
		background: none;
		border: 0;
		width: 2em;
		height: 2.5em;
		cursor: pointer;
		z-index: 1; }

	.searchFlyout {
		position: absolute;
		background: $color-grey1;
		border: 1px solid $color-grey4;
		display: block;
		padding: 1em;
		max-width: 35.3em;
		font-size: .9em;
		z-index: 1;
		display: none;

		&.active {
			display: block; }

		h4 {
			font-size: 1em; }

		&.filtered h4 {
			display: none; }

		p {
			border-bottom: 1px dotted $color-grey3;
			margin: 0 0 .5em 0;
			padding: 0;
			text-align: left; }

		a {
			color: $color-title; }

		div {
			display: none;

			&.active {
				display: block; } }

		.searchFilter p {
			background: url(/assets/images/iconSearch.png) no-repeat -2px -402px;
			background-image: url(/assets/images/iconSearch.svg), none;
			padding-left: 20px;

			&.product {
				background-position: -2px -702px; }
			&.technicalDocument {
				background-position: -2px -1002px; }
			&.support {
				background-position: -2px -1302px; }
			&.crossReference {
				background-position: -2px -1602px; }
			&.application {
				background-position: -2px -1902px; }
			&.forum {
				background-position: -2px -2202px; }
			&.blog {
				background-position: -2px -2502px; }
			&.designNetwork {
				background-position: -2px -2802px; }
			&.developerWiki {
				background-position: -2px -3102px; }
			&.training {
				background-position: -2px -3402px; }
			&.video {
				background-position: -2px -3702px; } }

		.searchHistory {
			margin: 2em 0 0 0; }

		&.focused {
			right: 0;
			width: 35.3em;

			.searchFilter p {
				display: none;

				&.product, &.technicalDocument, &.support {
					display: block; } } } }

	&.simplified {
		border-bottom: 8px solid $color-red4; } }

footer#tiFooter {
	clear: both;
	color: $color-grey5;
	font-size: .9em;
	padding: 1em $page-margin-ie;
	padding: 1em $page-margin;
	border-top: 1px solid $color-grey3;
	background: $color-white;

	p {
		max-width: none; }
	a {
		color: $color-grey5;

		&:hover {
			color: $color-red4; } }

	.link {
		float: left;
		width: 80%;

		a.mobileSite {
			background: url(/assets/images/mobile.gif) no-repeat 0 0;
			padding: 5px 0 0 14px; } }

	.social {
		float: right;

		li {
			float: left;
			background: none;
			padding: 0;
			margin: 0 0 0 1em;

			a {
				background: url(/assets/images/icons/icon_follow-us-facebook.png) no-repeat 0 0;
				display: block;
				width: 16px;
				height: 16px; } }

		li#twitter {
			a {
				background-image: url(/assets/images/icons/icon_follow-us-twitter.png); } }

		li#google {
			a {
				background-image: url(/assets/images/icons/icon_google.png); } }

		li#linkedin {
			a {
				background-image: url(/assets/images/icons/icon_linkedin.png); } }

		li#e2e {
			a {
				background-image: url(/assets/images/icons/e2e_footer.png); } }

		li#weibo {
			a {
				background-image: url(/assets/images/icons/sina_weibo_icon_cn_footer.png); } }
		li#cne2e {
			a {
				background-image: url(/assets/images/icons/deyisupport_icon_cn_footer.png); } } } }

.test_delete_later {
	background-color: blue; }
