.icon-wordreplace {
	display: inline-block;
	position: relative;
	padding: 0 1.2em;
	right: 9999px;
	min-width: 1.2em;
	max-width: 1.2em; }

.icon-wordreplace:after {
	position: absolute;
	overflow: visible;
	right: -9999px;
	width: 100%;
	text-align: center;
	font-size: 1.5em;
	line-height: 1;
	font-family: 'Material Icons', sans-serif;
	bottom: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-rendering: optimizeLegibility; }

a.icon-wordreplace:after {
	color: $color-teal5; }

a.icon-wordreplace:hover:after {
	color: $color-red4; }

.icon-check:after {
	content: '\e86c'; }

.icon-dash:after {
	content: '\e15b'; }

.icon-plus:after {
	content: '\e147'; }

.icon-minus:after {
	content: '\e15c'; }

.icon-delete:after {
	content: '\e872'; }

.icon-search:after {
	content: '\e8b6'; }

.icon-edit:after {
	content: '\e3c9'; }

.icon-colorteal {
	color: $color-teal5; }

.icon-colorgrey {
	color: $color-grey6; }

.icon-colorred {
	color: $color-red4; }
