label {
	display: inline-block;
	vertical-align: middle; }

input, select, textarea {
	font-size: 1em;
	color: $color-body; }

input[type=text], input[type=password], select, textarea {
	padding: .2em;
	border: 1px solid $color-grey4;
	border-radius: .2em;
	margin: 0;
	outline: 0;
	-webkit-transition: border .2s;
	transition: border .2s;

	&:focus {
		border-color: $color-body; } }

input.search-button {
	vertical-align: middle;
	width: 28px;
	height: 28px;
	margin: 0 0 0 -30px;
	padding: 0;
	background: url(/assets/images/iconFolder.png) no-repeat 5px -394px;
	background-image: url(/assets/images/iconFolder.svg), none;
	border: 0;
	cursor: pointer; }

span.optional {
	/* display: block */
	color: $color-grey5;
	font-style: italic;
	font-size: .8em; }

/* used to be div.info*/
.icon-info {
	position: relative;
	display: inline-block;
	min-width: 18px;
	min-height: 18px;
	vertical-align: middle;
	cursor: pointer;
	background: url(/assets/images/iconMyTIinfoSprite.png) no-repeat 0 0;
	background-image: url(/assets/images/iconMyTIinfoSprite.svg), none;
	padding: 0 0 0 22px; }

