form.responsive {
	.row {
		margin-bottom: 1em; }

	.indent {
		padding-left: 2em; } }

.error-check {
	position: relative;
	padding: .2em 0 0 1.2em;
	color: $color-error;

	&:before {
		position: absolute;
		background: url(/assets/images/ic-form-error.png) no-repeat 0 0;
		background-image: url(/assets/images/ic-form-error.svg), none;
		background-size: 100%;
		top: .4em;
		left: 0;
		width: 1em;
		height: 1em;
		content: " "; }

	&.summary {
		background-color: $color-red2;
		padding: 1em 1em 1em 3.3em;
		border: 1px solid $color-red5;
		border-radius: .2em;

		&:before {
			top: .7em;
			left: 1em;
			width: 2em;
			height: 2em; } }

	&.success {
		background-color: $color-white;
		color: $color-title;
		border-color: $color-success;

		&:before {
			background-image: url(/assets/images/ic-form-success.png);
			background-image: url(/assets/images/ic-form-success.svg), none; } } }
