.pageGroup {
	padding: 2em 0; }

.siloTitle {
	font-size: 1.6em;
	color: $color-title;
	padding: 0 $page-margin-ie 1em;
	padding: 0 $page-margin 1em; }

.pageTitle {
	padding: 0 $page-margin-ie;
	padding: 0 $page-margin; }

.pageNav ~ .pageTitle {
	float: left;
	width: 80%; }

.breadcrumb, .breadcrumbs {
	font-size: .9em;
	color: $color-grey5;

	a/*, a:visited*/ {
		color: $color-grey5; } }

.breadcrumb {
	padding: 0 $page-margin-ie 1em;
	padding: 0 $page-margin 1em; }

.pageTitle .breadcrumb {
	padding: 0; }

.pageContent {
	position: relative;
	padding: 0 $page-margin-ie;
	padding: 0 $page-margin; }

.pageContent, #ls-row-3-col-2, #ls-row-3-area-2 {
	a:link:hover {
		color: $color-link-hover; }

	a:visited {
		color: $color-link-visited; } }

.pageNav ~ .pageContent, .pageNav ~ form .pageContent {
	float: left;
	width: 80%; }

.pageContent1 {
	float: left;
	width: 70%; }

.pageContent2 {
	float: right;
	width: 30%;
	padding: 0 0 0 3em; }

.pageGroup .pageNav, #ls-gen7-ls-lqr-w-fx, .InteriorTwoColLeftRail-LeftRail {
	z-index: 2;
	position: relative;
	float: left;
	width: 20%;
	background: $color-grey1;
	border: 1px solid $color-grey3;
	border-width: 1px 1px 1px 0;
	padding: 2em $page-margin-ie;
	padding: 2em $page-margin;
	margin: 0 0 2em 0;

	.collapse-left, .collapse {
		cursor: pointer;

		&:before {
			float: right;
			background: $color-body;
			border-radius: 50%;
			color: $color-white;
			font-size: 1.4em;
			margin: -1rem -1.5rem 0 0;
			content: "\e5c4"; } } }

//#cmpntnav for some wrapper sites e,g, http://www.ti.com/ww/tw/prod_clocks.html
.pageNavContent, #cmpntprodtree, #flyList, #cmpntnav {
	.cntboxhead > * {
		font-size: 1.2em;
		color: $color-body; }

	a {
		display: inline-block;
		color: $color-title; }

	/* h3
		border-bottom: 1px dotted $color-grey3
		margin-bottom: 1em */

	ul {
		margin: 0; }

	.cntboxbody > ul, > ul {
		border-bottom: 1px dotted $color-grey3;
		margin-bottom: 3em; }

	li {
		background: none;
		border-top: 1px dotted $color-grey3;
		margin: 0;
		padding: .4em 0;

		ul {
			margin: .4em 0 -.4em; }

		li a, a.appcategindent {
			padding-left: 1em; }

		li li a, a.indent {
			background: url(/assets/images/ulDash.gif) no-repeat 1.5em .75em;
			padding-left: 2em; }

		&.active > a, a.active {
			color: $color-red4;
			font-weight: $font-weight-bold; } }

	/* h3, li
		span
			font-size: 10px
			font-weight: $font-weight-normal
			margin: 0 0 0 5px */ }

.pageNavContent {
	.related {
		background: $color-white;
		padding: 1em;
		border: 1px solid $color-grey3;
		margin-top: 3em;

		> h4 {
			border-bottom: 1px solid $color-grey5;
			padding-bottom: .4em;
			margin-bottom: 0; }

		> ul {
			border-bottom: 1px dotted $color-grey3;

			> li:first-child {
				border: 0; } }

		a {
			color: $color-link; }

		b {
			display: block;
			padding: .4em 0; } } }

.navCollapsed .pageNav, #ls-canvas.collapsed #ls-gen7-ls-lqr-w-fx, #ls-canvas.collapsed .InteriorTwoColLeftRail-LeftRail {
	position: absolute;
	width: auto;
	padding-left: $page-margin-ie / 10;
	padding-left: $page-margin / 10;
	padding-right: $page-margin-ie / 10;
	padding-right: $page-margin / 10;
	z-index: 2;

	.collapse-left, .collapse {
		position: relative;
		display: inline-block;
		padding: 1.5em 0 0;

		&:before {
			background: none;
			color: $color-body;
			position: absolute;
			top: 0;
			left: .2em;
			margin: 0;
			content: "\e5d2";
			font-size: 24px; }

		&:after {
			font-size: .9em;
			content: "Menu";
			display: block;
			margin: .5em 0 0;
			text-align: center; }

		&.cn:after {
			content: "\83dc\5355";
			width: 3em; }

		&.jp:after {
			content: "\30e1\30cb\30e5\30fc";
			font-size: .85em; }

		&.kr:after {
			content: "\ba54\b274";
			width: 3em; }

		&.de:after {
			content: "\4d\65\6e\fc";
			width: 3em; } }

	.pageNavContent, > div {
		display: none; } }

.navCollapsed .pageTitle, .navCollapsed .pageContent, #ls-canvas.collapsed #ls-row-3-col-2, #ls-canvas.collapsed #ls-row-3-area-2 {
	width: 100%;
	// padding-left: 60px
	padding-left: 4em; }

.pageNav .close {
	padding: 11px;
	position: absolute;
	top: .5em;
	right: 0;
	background: url(/assets/images/iconCloseModal.png) no-repeat 0 0;
	background-image: url(/assets/images/iconCloseModal.svg), none;
	cursor: pointer;

	&:active {
		background-position: 0 -100px; } }

.tab {
	position: relative;
	width: 100%;
	border-bottom: 1px solid $color-body;

	a {
		color: $color-title !important;
		text-decoration: none;
		cursor: pointer; }

	ul:before, ul:after {
		content: "";
		display: table; }

	ul:after {
		clear: both; }

	ul {
		margin: 0;
		padding: 0 $page-margin-ie;
		padding: 0 $page-margin;
		display: flex;
		align-items: stretch; }

	li {
		background: url(/assets/images/iconTab.png) no-repeat 0 0;
		position: relative;
		float: left;
		margin: 0 1.4em 0 -.7em;
		padding: .7em .1em .7em .7em;
		color: $color-grey4;

		&:after {
			background: url(/assets/images/iconTab.png) no-repeat right 0;
			position: absolute;
			top: 0;
			right: -24px;
			width: 25px;
			height: 100%;
			content: ""; }

		&.active {
			font-weight: $font-weight-bold;
			background-position: 0 -120px;
			z-index: 1;

			a {
				color: $color-white !important; }

			&:after {
				background-position: right -120px; } } }

	li, li:after {
		background-image: url(/assets/images/iconTab.svg), none; } }

.anchor {
	border-bottom: 1px solid $color-grey4;
	padding: 1em 0 .7em 0;

	ul {
		margin: 0;
		padding: 0 $page-margin-ie;
		padding: 0 $page-margin; }

	li {
		background: none;
		float: left;
		padding: 0;
		color: $color-grey4;
		margin: 0;

		&:after {
			content: "|";
			padding: 0 .5em; }

		&:last-child:after {
			content: none; }

		&.active {
			a {
				background: $color-body;
				color: $color-white;
				padding: 1px 10px; } } } }

.pagination {
	margin: 1em 0;
	text-align: right;
	cursor: default;

	a {
		cursor: pointer; }

	span {
		margin: 0 0 0 2em;

		&.active {
			border: 1px solid $color-body;
			border-radius: .2em;
			padding: 0 .5em; } } }
