table {
	border-collapse: collapse;
	font-size: 1em; }

.tblstandard, table.sort {
	position: relative;
	/*z-index: 1*/
	border-top: 1px solid $color-grey3;
	border-left: 1px solid $color-grey3;
	font-size: 1em;
	margin: 0 0 1em 0;

	th, td {
		background: $color-white;
		border-right: 1px solid $color-grey3;
		border-bottom: 1px solid $color-grey3;
		vertical-align: top;
		padding: .5em;

		&.image {
			vertical-align: middle;
			text-align: center; } }

	th {
		background: $color-grey2;
		text-align: left;
		vertical-align: middle;
		color: $color-title; } }

.tblstandard {
	tr.heading2 th {
		background: $color-grey5; }

	tr.heading3 th {
		background: $color-teal6;

		&, a, a:hover, a:visited {
			color: $color-white !important; } }

	tr.hilighted td, td.hilighted {
		background: $color-yellow1; } }

table.noBorder {
	td {
		border: 0; } }

table.sort {
	th.header {
		background: $color-grey2 url(/assets/images/sort.png) no-repeat right center;
		cursor: pointer;
		text-align: left;
		padding-right: 20px;
		vertical-align: middle; }

	th.headerSortUp {
		background: $color-grey4 url(/assets/images/sortAsc.png) no-repeat right center; }
	th.headerSortDown {
		background: $color-grey4 url(/assets/images/sortDesc.png) no-repeat right center; } }

// tools & sw component - section for refining search results
// e.g, http://www.ti.com/lsds/ti/dsp/keystone_arm/toolsw.page
// Need to be removed when the component is removed.
.control_table {
	width: auto !important;
	background: $color-grey2;
	border: 1px solid $color-grey3;
	border-bottom: 0;

	td {
		padding: 1em 1em 0 !important;
		white-space: nowrap; }

	.match {
		font-weight: $font-weight-bold;
		margin: 0 0 1em 0 !important; } }


//	update to comparison table
.tblstandard.tblcomparison {
	line-height: 1.5em;
	border: 0;
	width: 100%;
	border-bottom: 1px solid $color-grey3;

	th, td {
		background: none;
		color: #444;
		text-align: center;
		border-left: 0;
		border-right: 0; }

	thead tr {
		border-left: 0; }

	thead tr td:first-child, th {
		border-bottom: 0; }

	td, th {
		padding-top: 8px;
		padding-bottom: 8px;
		border-right: 0; }

	tbody tr:first-child {
		border-left: 0;
		border-top: 1px solid #118899; }

	tbody tr > th {
		background: none;
		text-align: left;
		font-weight: 700;
		color: $color-grey6;
		border-right: 0;
		border-bottom: 1px solid $color-grey3;
		background-color: $color-grey1; }

	tbody th[colspan] {
		font-size: 1.2em;
		color: $color-black; }

	tbody tr > th[scope='row'] {
		background: transparent; }

	tbody tr > td:last-child {
		text-align: center; }

	tbody tr:last-child th, tbody tr:last-child td {
		border-bottom: 0; }

	span.tblIndicator {
		display: inline-block;
		position: relative;
		top: 3px; }

	img {
		height: auto; } }

//	hover highlight row behaviour
.tblstandard.tblcomparison.hover-row {
	tbody tr:hover {
		background: $color-teal1; } }
